<template>
  <div class="page-owasp-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/waf-auth' }">系统配置</el-breadcrumb-item>
        <el-breadcrumb-item>基础信息</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-form :model="baseConfigForm" :rules="rules" ref="baseConfigForm" label-width="120px">
          <el-form-item label="waf_auth" prop="waf_auth" key="1">
            <el-input v-model="baseConfigForm.waf_auth" placeholder="请输入waf_auth" class="global-pwd">
            </el-input>
            <el-button icon="el-icon-refresh" @click="onClickRefresh"></el-button>
          </el-form-item>

        </el-form>
        <el-row type="flex" class="margin-border" justify="space-between">
          <el-col :span="12"></el-col>
          <el-col :span="12" class="text-align-right">
            <el-button type="primary" @click="onClickBaseConfigSubmit('baseConfigForm')" :loading="loading">保存
            </el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: false,
      loading: false,
      baseConfigForm: {},
    };
  },
  computed: {
    rules() {
      return {
        waf_auth: [
          {
            required: true,
            message: '请输入waf_auth',
            trigger: ["blur", "change"],
          },
        ],
      }
    }
  },

  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      var getUrl = "/waf/waf_get_waf_auth";

      JXAjax(
        "post",
        getUrl,
        {},
        function (response) {
          t.loadingPage = false;
          t.baseConfigForm.waf_auth = response.data.waf_auth;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },

    onClickBaseConfigSubmit(baseConfigForm) {
      var t = this;
      var postUrl = "/waf/waf_edit_waf_auth";
      this.$refs[baseConfigForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.baseConfigForm,
            function (response) {
              t.loading = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    onClickRefresh() {
      function S4() {
        return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
      }
      function get_uuid() {
        return (
          S4() +
          S4() +
          "-" +
          S4() +
          "-" +
          S4() +
          "-" +
          S4() +
          "-" +
          S4() +
          S4() +
          S4()
        );
      }
      this.baseConfigForm.waf_auth = get_uuid();
    },
  },
};
</script>
<style>
.page-owasp-wrap {
  max-width: 800px;
  min-width: 400px;
}

.page-owasp-wrap .match-inline-block {
  width: 192px;
}

.engine-form .el-form-item__content {
  margin-left: 40px;
}

.page-owasp-wrap .global-pwd {
  width: calc(100% - 60px);
  margin-right: 4px;
}
</style>
